<template>
  <div class="router">
    <div class="card">
      <div class="card-header">
        
      </div>
      <div class="main">
        <el-form ref="form"
                 :model="form"
                 label-width="80px">
          <el-form-item label=""
                        prop="title"
                        :rules="[{ required: true, message: ''}]">
            <el-input v-model="form.title"></el-input>
          </el-form-item>
          <el-form-item label=""
                        prop="describe"
                        :rules="[{ required: true, message: 'describe'}]">
            <el-input type="textarea"
                      v-model="form.describe"></el-input>
          </el-form-item>
          <el-form-item label=""
                        prop="remark"
                        :rules="[{ required: true, message: ''}]">
            <el-input v-model="form.remark"
                      placeholder="99"></el-input>
          </el-form-item>

          <el-form-item label=""
                        prop="category"
                        :rules="[{ required: true, message: ''}]">
            <el-select v-model="form.category"
                       placeholder="">
              <el-option v-for="item in catagory"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=""
                        prop="brand_id"
                        :rules="[{ required: true, message: ''}]">
            <el-select filterable v-model="form.brand_id"
                       placeholder="">
              <el-option v-for="item in brand"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=""
                        prop="brand"
                        :rules="[{ required: true, message: ''}]">
            <el-input placeholder=""
                      v-model="form.brand"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-switch v-model="isopen"
                       @change="changeOpen"></el-switch>
          </el-form-item>
          <el-form-item label="">
            <el-switch v-model="ishot"
                       @change="changeHot"></el-switch>
          </el-form-item>
          <el-form-item label=""
                        prop="number"
                        :rules="[{ required: true, message: ''}]">
            <el-input placeholder=""
                      v-model="form.number"></el-input>
          </el-form-item>
          <el-form-item label=""
                        prop="sell"
                        :rules="[{ required: true, message: ''}]">
            <el-input placeholder=""
                      v-model="form.sell"></el-input>
          </el-form-item>
          <el-form-item label=""
                        prop="image"
                        :rules="[{ required: true, message: ''}]">
            <el-upload class="upload-demo"
                       :action="action"
                       :headers="headers"
                       :on-success="imageSuccess"
                       :on-remove="imageRemove"
                       list-type="picture">
              <el-button size="small"
                         type="primary"></el-button>
              <div slot="tip"
                   class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-form-item>
          <el-form-item label="banner"
                        prop="banner"
                        :rules="[{ required: true, message: ''}]">
            <el-upload class="upload-demo"
                       :action="action"
                       :headers="headers"
                       :on-success="bannerSuccess"
                       :on-remove="bannerRemove"
                       list-type="picture">
              <el-button size="small"
                         type="primary"></el-button>
              <div slot="tip"
                   class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-form-item>
          <el-form-item label="">
            <div class="card">
              <el-table :data="attribute"
                        style="width: 100%">
                <el-table-column label="">
                  <template slot-scope="scope">
                    <input v-model="scope.row.title"
                           class="input"
                           placeholder="" />

                  </template>
                </el-table-column>
                <el-table-column prop="price"
                                 label="">
                  <template slot-scope="scope">
                    <input v-model="scope.row.price"
                           class="input"
                           placeholder="" />
                  </template>
                </el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <input v-model="scope.row.cprice"
                           class="input"
                           placeholder="" />
                  </template>
                </el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <input v-model="scope.row.jdprice"
                           class="input"
                           placeholder="" />
                  </template>
                </el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <input v-model="scope.row.number"
                           class="input"
                           placeholder="" />
                  </template>
                </el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <el-button type="danger"
                               size="mini"
                               icon="el-icon-delete"
                               @click="attrDelete(scope.$index)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
              <div class="add">
                <el-button class="add-button"
                           type="primary"
                           icon="el-icon-circle-plus-outline"
                           @click="attrAdd">
                </el-button>
              </div>
            </div>
          </el-form-item>
          <el-form-item label=""
                        prop="content"
                        :rules="[{ required: true, message: ''}]">
            <quill-editor :options="editor"
                          v-model="form.content"></quill-editor>
          </el-form-item>
          <el-form-item>
            <el-button type="primary"
                       @click="onSubmit('form')"></el-button>
            <el-button></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import { getData } from "../../untils/js/request"

import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor, Quill } from 'vue-quill-editor'
import { container, ImageExtend, QuillWatch } from 'quill-image-extend-module'

Quill.register('modules/ImageExtend', ImageExtend)
export default {
  components: {
    quillEditor
  },
  name: "add-article",
  data () {
    return {
      data: [],
      action: this.Base + '/admin/tools/upload',
      headers: { 'token': '' },
      images: [],
      editor: {
        modules: {
          ImageExtend: {
            loading: true,
            name: 'file',
            action: this.Base + '/admin/tools/upload',
            response: (res) => {
              return this.Base + res.url
            }
          },
          toolbar: {
            container: container,
            handlers: {
              'image': function () {
                QuillWatch.emit(this.quill.id)
              }
            }
          }
        }
      },
      isopen: false,
      ishot: false,
      form: {
        'title': '',
        'category': '',
        'remark': '',
        'types': '',
        'brand_id': '',
        'brand': '',
        'image': '',
        'isopen': 0,
        'ishot': 0,
        'number': '',
        'sell': '',
        'banner': '',
        'content': ''
      },
      catagory: [],
      brand: [],
      attribute: [{ 'title': '', 'price': '', 'cprice': '','jdprice': '', 'discount': 8, 'number': 100 }],
    }
  },
  mounted () {
    this.headers.token = localStorage.getItem('token')
    this._getCategory()
  },
  methods: {
     async _getCategory () {
      let res = await getData('/category?list=1')
      if (res.error_code === 10000) {
        this.active = res.data.active
        this.catagory = res.data.category
        this.brand = res.data.brand
      }
    },
    async _create () {
      let data = await getData('/goods/save/', { 'form': this.form, 'attribute': this.attribute }, 'POST')
      if (data.error_code === 10000) {
        this.$message('')
      }
    },
    changeOpen (e) {
      this.form.isopen = e ? 1 : 0
    },
    changeHot (e) {
      this.form.ishot = e ? 1 : 0
    },
    imageSuccess (e) {
      this.form.image = e.url
    },
    imageRemove () {
      this.form.image = ''
    },
    bannerSuccess (e) {
      this.images.push(e.url)
    },
    bannerRemove (res, file) {
      this.images = []
      file.map((item) => {
        this.images.push(item.url)
      })
    },
    attrAdd () {
      this.attribute.push({ 'title': '', 'price': '', 'cprice': '', 'jprice': '', 'discount': 8, 'number': 100 })
    },
    attrDelete (key) {
      this.attribute.splice(key, 1)
    },
    onSubmit (form) {
      this.form.banner = this.images.join(',')
      this.$refs[form].validate((valid) => {
        if (valid) {
          this.form.isopen = this.form.isopen ? 1 : 0
          this._create()
        } else {
          return false;
        }
      });
    }
  }
}
</script>

