<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <!---->
        <div class="create">
          <span>: {{total}} </span>
          <span>: {{price}}</span>
        </div>

        <!---->
        <div class="card-body text">
          <el-table :data="data"
                    style="width: 100%">
            <el-table-column prop="id"
                             label="ID"
                             width="120">
            </el-table-column>
            <el-table-column prop="amount" width="140"
                             label="">
            </el-table-column>
            <el-table-column prop="notePub"
                             label="">
            </el-table-column>
            <el-table-column prop="tradeTypeName" width="180"
                             label="">
            </el-table-column>
            <el-table-column prop="createdDate"
                             label="">
            </el-table-column>
          </el-table>
        </div>
      </div>
      <!---->
      <div class="page">
        <el-pagination @current-change="next"
                       background
                       layout="prev, pager, next"
                       :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getData } from "../../untils/js/request"

export default {
  name: "jdorder",
  data () {
    return {
      data: [],
      layer: { 'order': false, 'status': true },
      form: {},
      price: 0,
      total: 0
    }
  },
  created () {
    this._getData()
  },
  methods: {
    async _getData () {
      let data = await getData(`/order/jdorder`, '')
      if (data.error_code === 10000) {
        this.data = data.data.data
        this.price = data.price
        this.total = data.data.total
      }
    },
    async _sumbit () {
      let data = await getData(`/order/update/id/${this.form}`, this.form)
      if (data.error_code === 10000) {
        this.$message({
          type: 'success',
          message: '!'
        })
        this.layer.order = false
      }
    },
    async _del (id, key) {
      let data = await getData(`order/delete?id=${id}`, {}, 'delete')
      if (data.error_code === 10000) {
        this.data.splice(key, 1)
        this.$message({
          type: 'success',
          message: '!'
        })
      }
    },
    next (e) {
      this.page = e
      this._getData()
    }
  }

}
</script>

<style scoped>
.el-form {
  margin-top: 20px;
}
.el-form-item__label {
  width: 70px;
}
.el-input {
  width: 200px;
}
</style>