<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <!---->
        <div class="search" style="display:flex">
          <el-select v-model="cate" placeholder="" style="margin-right:5px;">
            <el-option
              v-for="(val, key) in options"
              :key="key"
              :label="val.label"
              :value="val.value"
            >
            </el-option>
          </el-select>
          <el-input
            placeholder=""
            v-model="title"
            class="input-with-select"
          >
            <el-button slot="append" icon="el-icon-search" @click="search"
              ></el-button
            >
          </el-input>
        </div>
        <!---->
        <div class="create">
          <div style="display:flex">
            <el-button type="primary" icon="el-icon-edit" @click="create"
              ></el-button
            >
            <el-button type="success" @click="changeStatus(1)"></el-button>
            <el-button type="danger" @click="changeStatus(0)"></el-button>
          </div>
          <span>: {{ total }} </span>
        </div>
        <!---->
        <div class="card-body text">
          <el-table :data="data" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"> </el-table-column>
            <el-table-column prop="id" width="80" label="ID"></el-table-column>
            <el-table-column prop="title" label="">
              <template slot-scope="scope">
                <span :title="scope.row.title" v-text="scope.row.title"></span>
              </template>
            </el-table-column>
            <el-table-column prop="category_title" width="120" label="">
            </el-table-column>
            <el-table-column prop="attr[0].price" width="80" label="">
            </el-table-column>
            <el-table-column
              class="danger"
              prop="attr[0].cprice"
              width="80"
              label=""
            >
            </el-table-column>
            <el-table-column prop="attr[0].jdprice" width="80" label="">
            </el-table-column>
            <el-table-column prop="sort" width="100" label="">
              <template slot-scope="scope">
                <el-input
                  title=""
                  placeholder=""
                  v-model="scope.row.sort"
                  @blur="changeSort(scope.row.id, scope.row.sort)"
                ></el-input>
              </template>
            </el-table-column>
            <el-table-column prop="brand" width="80" label="">
            </el-table-column>
            <el-table-column label="" width="180">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-top"
                  v-if="!scope.row.status"
                  title=""
                  @click="change(scope.row.id, scope.$index, 1)"
                >
                </el-button>
                <el-button
                  size="mini"
                  type="info"
                  title=""
                  icon="el-icon-bottom"
                  v-else
                  @click="change(scope.row.id, scope.$index, 0)"
                >
                </el-button>
                <el-button
                  size="mini"
                  type="success"
                  icon="el-icon-edit"
                  @click="_edit(scope.row.id)"
                >
                </el-button>
                <el-button
                  @click="del(scope.row.id, scope.$index)"
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                >
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <!---->
      <div class="page">
        <el-pagination
          background
          :page-size="pagsize"
          layout="prev, pager, next"
          @current-change="handleCurrentChange"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getData } from '../../untils/js/request'
import store from '../../store'
export default {
  name: 'index',
  data() {
    return {
      data: [],
      page: 1,
      idAll: [],
      options: [],
      pagsize: 20,
      cate:'',
      title: '',
      total: 0
    }
  },
  created() {
    this._getData()
    this._getCategory()
  },
  methods: {
    async _getData() {
      let res = await getData(
        `/goods/index?page=${this.page}&title=${this.title}&types=${this.cate}`,
        ''
      )
      if (res.error_code === 10000) {
        this.data = res.data.data
        this.total = res.data.total
        this.page = parseInt(res.data.current_page)
      } else {
        this.data = []
        this.total = 0
        this.page = 0
      }
    },
    async _getCategory() {
      let res = await getData(`/category/index?list=1`, '')
      if (res.error_code === 10000) {
        res.data.category.map(item => {
          this.options.push({
            value: item.value,
            label: item.label
          })
        })
      }
    },
    create() {
      store.commit('add_tabs', { path: 'add-goods', name: '' })
      this.defaultTab = this.$route.path
    },
    async _change(id, key, status) {
      let res = await getData(
        `/goods/update/id/${id}`,
        { form: { status: status } },
        'POST'
      )
      if (res.error_code === 10000) {
        this.data[key]['status'] = status
        this.$message('')
      } else {
        this.$message.error('')
      }
    },
    async _changeAll(status) {
      let res = await getData(
        `/goods/updateAll`,
        { status: status, id: this.idAll },
        'POST'
      )
      if (res.error_code === 10000) {
        this.$message.success('')
      } else {
        this.$message.error('')
      }
    },
    async _detele(id, key) {
      let res = await getData(`/goods/delete/id/${id}`)
      if (res.error_code === 10000) {
        this.$message('')
        this.data.splice(key, 1)
      }
    },
    async _changeSort(id, sort) {
      let res = await getData(`/goods/updates/id/${id}`, { sort: sort }, 'POST')
      if (res.error_code === 10000) {
        this.$message.success('')
      }
    },
    changeSort(id, val) {
      this._changeSort(id, val)
    },
    changeStatus(status) {
      if (this.idAll.length) {
        this._changeAll(status)
      } else {
        this.$message.error('')
      }
    },
    handleSelectionChange(e) {
      this.idAll = []
      e.map(item => {
        this.idAll.push(item.id)
      })
    },
    change(id, key, status) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this._change(id, key, status)
      })
    },
    del(id, key) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this._detele(id, key)
      })
    },
    _edit(id) {
      store.commit('setid', id)
      store.commit('add_tabs', { path: 'edit-goods', name: '' })
    },
    handleCurrentChange(e) {
      this.page = e
      this._getData()
    },
    search() {
      if (this.title||this.cate) {
        this._getData()
      } else {
        this.$message('')
      }
    }
  }
}
</script>

<style scoped></style>
