<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <!---->
        <div class="search"
             style="display:flex;">
          <el-select v-model="cate"
                     placeholder="">
            <el-option v-for="(val,key) in options"
                       :key="key"
                       :label="val.label"
                       :value="val.value">
            </el-option>
          </el-select>
          <el-input placeholder=""
                    v-model="title"
                    class="input-with-select">
            <el-button slot="append"
                       icon="el-icon-search"
                       @click="search"></el-button>
          </el-input>
        </div>
        <!---->
        <div class="create">
          <el-button type="primary"
                     icon="el-icon-edit"
                     @click="create"></el-button>
          <span>: {{total}} </span>
        </div>

        <el-dialog title=""
                   :visible.sync="layer.add"
                   :append-to-body="isBody">
          <el-button type="primary"
                     icon="el-icon-edit"
                     @click="createCategory()"></el-button>
          <div class="search" style="display:flex">
            <el-input placeholder=""
                      v-model="goodsTitle"
                      class="input-with-select"></el-input>
            <el-button slot="append"
                       icon="el-icon-search"
                       @click="searchTitle"></el-button>
          </div>
          <el-table :data="lists"
                    @selection-change="handleSelectionChange">
            <el-table-column type="selection"
                             width="55">
            </el-table-column>
            <el-table-column property="title"
                             label=""></el-table-column>
          </el-table>
          <div class="page">
            <el-pagination @current-change="handleChange"
                           background
                           page-size=20
                           layout="prev, pager, next"
                           :total="layer.total">
            </el-pagination>
          </div>
        </el-dialog>

           <!--  -->
        <el-dialog title=""
                   :visible.sync="layer.look"
                   :append-to-body="isBody">
          <el-button type="primary"
                     icon="el-icon-edit"
                     @click="removeCategory"></el-button>
          <div class="search"
               style="display:flex">
            <el-input placeholder=""
                      v-model="look.title"
                      class="input-with-select"></el-input>
            <el-button slot="append"
                       icon="el-icon-search"
                       @click="searchTitle"></el-button>
          </div>
          <el-table :data="look.data"
                    @selection-change="handleSelectionChange">
            <el-table-column type="selection"
                             width="55">
            </el-table-column>
            <el-table-column property="title"
                             label=""></el-table-column>
          </el-table>
          <div class="page">
            <el-pagination @current-change="handleLookChange"
                           background
                           page-size=20
                           layout="prev, pager, next"
                           :total="look.total">
            </el-pagination>
          </div>
        </el-dialog>

        <!---->
        <div class="card-body text">
          <el-table :data="data"
                    style="width: 100%">
            <el-table-column prop="id"
                             label="ID"
                             width="80">
            </el-table-column>
            
            <el-table-column prop="types" width="150"
                             label="">
              <template slot-scope="scope">
                <span v-if="scope.row.types===0"
                      style="color:#358180;"> </span>
                <span v-else-if="scope.row.types===1"
                      style="color:#fc5350;"> </span>
                <span v-else
                      style="color:#67c23a;"> </span>
              </template>
            </el-table-column>
            <el-table-column prop="title" 
                             label="">
            </el-table-column>
            <el-table-column prop="sort" width="180" label="">
              <template slot-scope="scope">
                <el-input
                  title=""
                  placeholder=""
                  v-model="scope.row.sort"
                  @blur="changeSort(scope.row.id, scope.row.sort)"
                ></el-input>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button size="mini"
                           type="primary"
                           icon="el-icon-top"
                           v-if="!scope.row.status"
                           title=""
                           @click="change(scope.row.id,scope.$index,1)">
                </el-button>
                <el-button size="mini"
                           type="info"
                           title=""
                           icon="el-icon-bottom"
                           v-else
                           @click="change(scope.row.id,scope.$index,0)">
                </el-button>
                <el-button size="mini"
                           type="success"
                           icon="el-icon-edit"
                           @click="_edit(scope.row.id)">
                </el-button>
                <el-button size="mini"
                           type="info"
                           icon="el-icon-plus"
                           @click="add(scope.row.id,scope.row.types,scope.row.cate_id)">
                </el-button>
                 <el-button size="mini"
                           type="success"
                           icon="el-icon-view"
                           @click="_look(scope.row.id,scope.row.id,scope.row.cate_id)">
                </el-button>
                <el-button @click="del(scope.row.id,scope.$index)"
                           type="danger"
                           icon="el-icon-delete"
                           size="mini">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <!---->
      <div class="page">
        <el-pagination @current-change="handleCurrentChange"
                       background
                       layout="prev, pager, next"
                       page-size=20
                       :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getData } from "../../untils/js/request"
import store from '../../store'

export default {
  name: "index",
  data () {
    return {
      data: [],
      lists: [],
      cate: 0,
      layer: { 'add': false, 'page': 0, 'total': 0, 'look': false },
      look: { data: [], 'page': 0, 'total': 0, 'title': '' },
      isBody: true,
      checkAll: false,
      isIndeterminate: true,
      goodsTitle: '',
      options: [{
        value: 0,
        label: ''
      }, {
        value: 1,
        label: ''
      }, {
        value: 2,
        label: ''
      }],
      types: 0,
      brand: 0,
      idAll: [],
      title: '',
      page: 1,
      total: 0
    }
  },
  created () {
    this._getData()
    this._getLists()
  },
  methods: {
    async _getData () {
      let res = await getData(`/category/index?page=${this.page}&title=${this.title}&cate=-1`, '')
      if (res.error_code === 10000) {
        this.data = res.data.data
        this.total = res.data.total
        this.page = res.data.current_page
      }
    },
    async _getLists () {
      let res = await getData(`/goods/index?page=${this.layer.page}&title=${this.goodsTitle}`, '')
      if (res.error_code === 10000) {
        this.lists = res.data.data
        this.layer.page = res.data.current_page
        this.layer.total = res.data.total
      }
    },
    create () {
      store.commit('add_tabs', { path: 'add-category', name: '' })
    },
    async _changeSort(id, sort) {
      let res = await getData(
        `/activity/updates/id/${id}`,
        { sort: sort },
        'POST'
      )
      if (res.error_code === 10000) {
        this.$message.success('')
      }
    },
    changeSort(id, val) {
      this._changeSort(id, val)
    },
    async _change (id, key, status) {
      let res = await getData(`/category/update/id/${id}`, { 'status': status }, 'POST')
      if (res.error_code === 10000) {
        this.data[key]['status'] = status
        this.$message('')
      } else {
        this.$message.error('')
      }
    },
    async _updateCate (status,types) {
      let res = await getData('goods/updateAllCategory/', { 'id': this.id, 'all': this.idAll,'types':types,'status':status }, 'POST')
      if (res.error_code === 10000) {
        this.$message.success('')
      }
    },
    add (id, types, cate_id) {
      this.idAll = []
      if (types == 2) {
        this.id = cate_id
      } else {
        this.id = id
      }
      this.types = types
      this.layer.add = true
    },
    handleSelectionChange (e) {
      this.idAll = []
      e.map((item) => {
        this.idAll.push(item.id)
      })
    },
    searchTitle () {
      if (this.goodsTitle) {
        this._getLists()
      } else {
        this.$message.error('')
      }
    },
    createCategory(){
      if (!this.idAll.length) {
        this.$message.error('')
      } else {
          var types='category'
          if (this.types == 2) {
            types = 'brand_id'
          }
        this._updateCate(1,types)
      }
    },
    removeCategory(){
      if (!this.idAll.length) {
        this.$message.error('')
      } else {
         var types='category'
          if (this.types == 2) {
            types = 'brand_id'
          }
        this._updateCate(0,types)
      }
    },
    async _detele (id, key) {
      let res = await getData(`/category/delete/id/${id}`)
      if (res.error_code === 10000) {
        this.$message('')
        this.data.splice(key, 1)
      }
    },
     async _getLook () {
      let res = await getData(`/goods/index?page=${this.layer.page}&title=${this.goodsTitle}&types=${this.types}&brand=${this.brand}`, '')
      if (res.error_code === 10000) {
        this.look.data = res.data.data
        this.layer.page = res.data.current_page
        this.look.total = res.data.total
      }
    },
      _look (id,types,brand) {
       this.idAll = []
      if (types == 2) {
        this.id = brand
      } else {
        this.id = id
      }
      this.types = types
      this.brand=brand
      this.layer.look = true
      this.layer.page=1
      this._getLook()
    },
    change (id, key, status) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this._change(id, key, status)
      })
    },
    del (id, key) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this._detele(id, key)
      })
    },
    _edit (id) {
      store.commit('setid', id)
      store.commit('add_tabs', { path: 'edit-category', name: '' })
    },
    handleCurrentChange (e) {
      this.page = e
      this._getData()
    },
    handleChange (e) {
      this.layer.page = e
      this._getLists()
    },
    handleLookChange (e) {
      this.layer.page = e
      this._getLook()
    },
    search () {
      if (this.title || this.cate) {
        this._getData()
      } else {
        this.$message('')
      }
    }
  }

}
</script>

