<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <!---->
        <div class="create">
          <el-button type="primary"
                     icon="el-icon-edit"
                     @click="create"></el-button>
          <span>: {{total}} </span>
        </div>
        <!---->
        <div class="card-body text">
          <el-table :data="data"
                    style="width: 100%">
            <el-table-column prop="id" width="50"
                             label="ID">
            </el-table-column>
            <el-table-column prop="title"
                             label="">
            </el-table-column>
            <el-table-column prop="url"
                             label="">
            </el-table-column>
            <el-table-column prop="sort" width="180" label="">
              <template slot-scope="scope">
                <el-input
                  title=""
                  placeholder=""
                  v-model="scope.row.sort"
                  @blur="changeSort(scope.row.id, scope.row.sort)"
                ></el-input>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <span v-if="scope.row.category===0"></span>
                <span v-if="scope.row.category===1"></span>
                <span v-if="scope.row.category===2"></span>
                <span v-if="scope.row.category===3">banner</span>
                <span v-if="scope.row.category===4">banner</span>
                <span v-if="scope.row.category===5"></span>
              </template>
            </el-table-column>
            <el-table-column prop="create_time"
                             label="">
            </el-table-column>
            <el-table-column label=""
                             width="120">
              <template slot-scope="scope">
                <el-button style="color:#409EFF;"
                           type="text"
                           size="small"
                           @click="openEdit(scope.row.id)">
                  
                </el-button>
                <el-button style="color:#F56C6C;"
                           @click="del(scope.row.id,scope.$index)"
                           type="text"
                           size="small">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <!---->
      <div class="page">
        <el-pagination @current-change="next"
                       background
                       layout="prev, pager, next"
                       page-size=20
                       :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getData } from "../../untils/js/request";
import store from '../../store';

export default {
  name: "index",
  data () {
    return {
      password: { 'new': '', 'old': '' },
      data: [],
      page:1,
      total: 0
    }
  },
  created () {
    this._getData()
  },
  methods: {
    async _getData () {
      let data = await getData(`/banner/index?page=${this.page}`, '')
      if (data.error_code === 10000) {
        this.data = data.data.data
        this.total=data.data.total
      }
    },
    look (src) {
      this.$alert(`<img src="${src}">`, '', {
        dangerouslyUseHTMLString: true
      });
    },
    async _del (id, key) {
      let data = await getData(`banner/delete?id=${id}`, {}, 'delete')
      if (data.error_code === 10000) {
        this.data.splice(key, 1)
        this.$message({
          type: 'success',
          message: '!'
        });
      }
    },
    async _changeSort(id, sort) {
      let res = await getData(
        `/banner/update/id/${id}`,
        { sort: sort },
        'POST'
      )
      if (res.error_code === 10000) {
        this.$message.success('')
      }
    },
    changeSort(id, val) {
      this._changeSort(id, val)
    },
    //  
    del (id, key) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this._del(id, key)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    // 
    openEdit (id) {
      store.commit('setid', id)
      store.commit('add_tabs', { path: 'edit-banner', name: 'banner' })
    },
    create () {
      store.commit('add_tabs', { path: 'add-banner', name: 'banner' })
    },
    next (e) {
      this.page = e
      this._getData()
    }
  }

}
</script>

