<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <!---->
        <div class="create">
          <el-button type="primary"
                     v-if="false"
                     icon="el-icon-edit"
                     @click="create"></el-button>
          <span>: {{total}} </span>
        </div>

        <!--  -->
        <el-dialog title=""
                   :append-to-body="layer.status"
                   :visible.sync="layer.order">
          <el-table :data="form.goods">
            <el-table-column property="goods_id"
                             label="ID"
                             width="150"></el-table-column>
            <el-table-column property="title"
                             label=""
                             width="150"></el-table-column>
            <el-table-column property="attr_title"
                             label=""></el-table-column>
            <el-table-column property="attr_price"
                             label=""></el-table-column>
            <el-table-column property="number"
                             label=""></el-table-column>
          </el-table>
          <el-form :model="form">
            <el-form-item label="">
              <el-input v-model="form.name"
                        autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.telephone"
                        autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.content"
                        autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.price"
                        :disabled="true"
                        autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.cprice"
                        :disabled="true"
                        autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-button style="color:#409EFF;"
                         v-if="form.status===1"
                         type="text">
                
              </el-button>
              <el-button style="color:red"
                         v-if="form.status===0"
                         type="text">
                ,
              </el-button>
              <el-button style="color:#fc5350;"
                         v-if="form.status===2"
                         type="text">
                
              </el-button>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="form.region"
                         placeholder="">
                <el-option label=""
                           value="shanghai"></el-option>
                <el-option label=""
                           value="beijing"></el-option>
                <el-option label=""
                           value="beijing"></el-option>
                <el-option label=""
                           value="beijing"></el-option>
                <el-option label=""
                           value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.number"
                        autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer"
               class="dialog-footer">
            <el-button @click="layer.order = false"> </el-button>
            <el-button type="primary"
                       @click="sumbit"
                       v-if="form.status!==1"> </el-button>
            <el-button type="primary"
                       @click="layer.order = false"
                       v-else> </el-button>
          </div>
        </el-dialog>

        <!---->
        <div class="card-body text">
          <el-table :data="data"
                    style="width: 100%">
            <el-table-column prop="id"
                             label="ID"
                             width="90">
            </el-table-column>
            <el-table-column prop="name"
                             label="">
            </el-table-column>
            <el-table-column prop="price"
                             label=""></el-table-column>
            <el-table-column prop="cprice"
                             label="">
              <template slot-scope="scope">
                <span v-if="scope.row.status"
                      style="color:red"
                      v-text="scope.row.cprice"></span>
                <span v-else
                      style="color:#ccc"></span>
              </template>
            </el-table-column>
            <el-table-column prop="create_time"
                             label="">
