            <el-table-column label="">
              <template slot-scope="scope">
                <el-button size="small"
                           @click="show(scope.row)">
                  
                </el-button>
              </template>
            </el-table-column>
            <el-table-column label=""
                             width="120">
              <template slot-scope="scope">
                <el-button style="color:#409EFF;"
                           v-if="scope.row.status===1"
                           type="text"
                           size="small">
                  
                </el-button>
                <el-button style="color:#ccc;"
                           v-if="scope.row.status===0"
                           type="text"
