                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <!---->
      <div class="page">
        <el-pagination @current-change="next"
                       background
                       layout="prev, pager, next"
                       :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import router from '../../router/index'
import { getData } from "../../untils/js/request"
import store from '../../store'

export default {
  name: "index",
  data () {
    return {
      data: [],
      layer: { 'order': false, 'status': true },
      form: {},
      total: 0
    }
  },
  created () {
    this._getData()
  },
  methods: {
    async _getData () {
      let data = await getData(`/order/index`, '')
      if (data.error_code === 10000) {
        this.data = data.data.data
        this.total = data.data.total
      }
    },
    async _sumbit () {
      let data = await getData(`/order/update/id/${this.form}`, this.form)
      if (data.error_code === 10000) {
        this.$message({
          type: 'success',
          message: '!'
        })
        this.layer.order = false
      }
    },
    async _del (id, key) {
      let data = await getData(`order/delete?id=${id}`, {}, 'delete')
      if (data.error_code === 10000) {
        this.data.splice(key, 1)
        this.$message({
          type: 'success',
          message: '!'
        })
      }
    },
    show (data) {
      this.form = data
      this.form.address = data.content + data.street
      this.layer.order = true
    },
    sumbit () {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this._sumbit()
      }).catch(() => {

      });
    },
    //  
    del (id, key) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this._del(id, key)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    // 
    openEdit (id) {
      store.commit('setid', id)
      store.commit('add_tabs', { path: 'edit-admin', name: '' })
    },
    async _password (id, text) {
      let data = await getData(`/admin/update?id=${id}`, { 'password': text }, 'POST')
      if (data.error_code === 10000) {
        localStorage.removeItem('token')
        router.push({ path: '/login', name: '' })
        window.location.reload()
      }
    },
    _change (id, text) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error',
        center: true
      }).then(() => {
        this._password(id, text)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    change (id) {
      this.$alert(``, ``, {
        dangerouslyUseHTMLString: true,
        showInput: true,
        inputPlaceholder: '',
        confirmButtonText: '',
      }).then((e) => {
        if (e.action === "confirm") {
          this._change(id, e.value)
        }
      });
      // this._password(id)
    },
    create () {
      store.commit('add_tabs', { path: 'add-admin', name: '' })
    },
    next (e) {
      this.page = e
      this._getData()
    }
  }

}
</script>

