<template>
  <div class="router">
    <div class="card">
      <div class="card-header">
        
      </div>
      <div class="main">
        <el-form ref="form"
                 :model="form"
                 label-width="80px">
          <el-form-item label=""
                        prop="category"
                        :rules="[{ required: true, message: ''}]">
            <el-select v-model="form.category"
                       placeholder="">
              <el-option v-for="item in allCategory"
                         :key="item.id"
                         :label="item.title"
                         :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=""
                        prop="title"
                        :rules="[{ required: true, message: ''}]">
            <el-input v-model="form.title"
                      placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=""
                        prop="describe"
                        :rules="[{ required: true, message: 'describe'}]">
            <el-input type="textarea"
                      v-model="form.describe"
                      placeholder=""></el-input>
          </el-form-item>

          <el-form-item label=""
                        prop="types"
                        :rules="[{ required: true, message: ''}]">
            <el-select v-model="form.types"
                       placeholder="">
              <el-option v-for="item in allTypes"
                         :key="item.id"
                         :label="item.title"
                         :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=""
                        prop="url"
                        :rules="[{ required: true, message: ''}]">
            <el-input v-model="form.url"
                      placeholder="pages/detail/main?id=IDpages/search/main?id=ID&title="></el-input>
          </el-form-item>
          <el-form-item label=""
                        prop="types"
                        :rules="[{ required: true, message: ''}]">
            <el-upload class="upload-demo"
                       :action="action"
                       :headers="headers"
                       :on-success="imageSuccess"
                       list-type="picture">
              <el-button size="small"
                         type="primary"></el-button>
              <div slot="tip"
                   class="el-upload__tip">jpg/png:500*200 :800*600 :550*470 banner:750*550</div>
            </el-upload>
          </el-form-item>
          <el-form-item>
            <el-button type="primary"
                       @click="onSubmit('form')"></el-button>
            <el-button></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import { getData } from "../../untils/js/request"

export default {
  components: {
  },
  name: "add-banner",
  data () {
    return {
      data: {},
      action: this.Base + '/admin/tools/upload',
      allCategory: [{ 'id': 0, 'title': '' }, { 'id': 1, 'title': '' }, { 'id': 2, 'title': '' }, { 'id': 3, 'title': 'banner' }, { 'id': 4, 'title': 'banner' },{ 'id': 5, 'title': '' }],
      allTypes: [{ 'id': 0, 'title': '' }, { 'id': 1, 'title': '' }],
      form: {
        'title': '',
        'describe': '',
        'types': '',
        'category': '',
        'url': '',
        'image': ''
      }
    }
  },
  mounted () {
  },
  methods: {
    async _create () {
      let data = await getData('/banner/save/', this.form, 'POST')
      if (data.error_code === 10000) {
        this.$message('')
      }
    },
    imageSuccess (e) {
      this.form.image = e.url
    },
    onSubmit (form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          this._create()
        } else {
          return false;
        }
      });
    }
  }
}
</script>

