router.beforeEach(function (to, from, next) {
    let token = localStorage.getItem('token')
    //
    if (!token) {
        if (to.path !== '/login') {
            return next({
                path: '/login',
                name: ''
            })
        }
        next()
    }
    // 
    if (to.path === '/login') {
        if (token) {
            return next({
                path: '/',
                name: ''
            })
        } else {
            return next({
                path: '/login',
                name: '',
            })
        }
    }
    next()
})
export default router